.chat {
  width: 100%;
  height: 100%;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #141414;
  border: 2px solid #df00001f;
  border-radius: 10px;
  &__messages-list {
    max-height: 120px;
    overflow-y: scroll;
    color: rgba(255, 255, 255, 0.555);
    font-size: 14px;
  }
  &__form {
    display: flex;
    height: 24px;
  }
  &__input {
    width: 93%;
    border: 2px solid rgba(0, 0, 0, 0.103);
    border-radius: 5px;
  }
  &__btn {
    position: relative;
    display: inline-block;
    font-family: Arial,Helvetica,FreeSans,"Liberation Sans","Nimbus Sans L",sans-serif;
    font-size: 0.5em;
    font-weight: 700;
    color: rgb(245,245,245);
    text-shadow: 0 -1px rgba(0,0,0,.1);
    text-decoration: none;
    user-select: none;
    padding: .3em 1em;
    outline: none;
    border: none;
    border-radius: 3px;
    box-shadow: 0 0 0 60px rgba(0,0,0,0) inset, .1em .1em .2em #800;
    background: linear-gradient(#FB9575, #F45A38 48%, #EA1502 52%, #F02F17);
    animation: pulsate 1.2s linear infinite;
  }
  button:hover {
    animation-play-state: paused;
    cursor: pointer;
  }
  button:active {
    top: 1px;
    color: white;
    text-shadow: 0 -1px rgba(0,0,0,.3), 0 0 5px rgb(255, 221, 221), 0 0 8px #fff;
    box-shadow: 0 -1px 3px rgba(0,0,0,.3), 0 1px 1px rgb(252, 4, 4), inset 0 1px 2px rgba(0,0,0,.8), inset 0 -1px 0 rgba(0,0,0,.05);
  }
  @-webkit-keyframes pulsate {
    50% {color: #fff; text-shadow: 0 -1px rgba(0,0,0,.3), 0 0 5px #ffd, 0 0 8px #fff;}
  }
  @keyframes pulsate {
    50% {color: #fff; text-shadow: 0 -1px rgba(0,0,0,.3), 0 0 5px #ffd, 0 0 8px #fff;}
  }
}

.chat__messages-list::-webkit-scrollbar {
  width: 0;
}
